import { Button } from 'bootstrap/dist/js/bootstrap.bundle'
import React, {useState} from 'react'



export default function Textform(props) {

    

     const handleUpClick =()=>{
        console.log("Uppercase was clicked"+text);
        let newText= text.toUpperCase();
        setText(newText)
        props.showAlert("converted to Upper case","success");
     }

     const handleLoClick =()=>{
      console.log("Lowercase was clicked"+text);
      let newText= text.toLowerCase();
      setText(newText)
      props.showAlert("converted to Lower case","success");
   }

   const handleClearClick =()=>{
      console.log("clear text");
      let newText= " ";
      setText(newText)
      props.showAlert("Text is Cleared","success");
   }
     const handleOnChange =(event)=>{
        console.log("On change");
        setText (event.target.value);
        
     }
        const [text , setText] = useState ('Enter text here2');
        
  return (
    <>
    <div className = "container" style={{ color:props.mode==='dark'?'white':'black'}}>
        <h1>{props.heading}</h1>
            <div className="mb-3">
            <label htmlFor="myBox" className="form-label"></label>
         <textarea className="form-control" value ={text}  onChange ={handleOnChange} style={{backgroundColor : props.mode==='dark'?'black':'white',color:props.mode==='dark'?'white':'black'}}  id="myBox" rows="8"></textarea>
            </div>
            <button className="btn btn-primary mx-2 "onClick={handleUpClick }>Upper</button>
            <button className="btn btn-primary mx-2" onClick={handleLoClick }>Lower</button>
            <button className="btn btn-primary mx-2" onClick={handleClearClick }>Clear</button>
    </div>

   <div className= "container my-5" style={{ color:props.mode==='dark'?'white':'black'}} >
      <h2>Your Text Summary</h2>
      <p>{text.split(" ").length} words and {text.length}characters</p>
      <p>{0.008 * text.split(" ").length} minutes to read words</p>
      <h2>Preview</h2>
      <p>{text.length>0?text:"Enter something to Preview"}</p>
   </div>
    </>
  )
}
